	data() {
		return {
			sexItem:['性别','男','女'],
			sexNum:0
		};
	},
	methods: {
		checkSex() {
			let that = this;
			uni.showActionSheet({
				itemList:['男','女'],
				success(res) {
					console.log(res)
					let idx = res.tapIndex;
					that.$data.sexNum = idx+1;
				}
			})
		}
	},
}
